* {
    background-color: rgb(35, 21, 21);
    color: white;

    margin-left: 5px;
}
.error {
    color: black;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}

.subheading {
    color: red;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 30px;
}

.title {
    color: red;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 40px;
}

.filter div input {
    padding: 10px;
    font-size: 15px;
    border-radius: 10px;
}

.filter div {
    font-size: 25px;
}

#contactForm div {
    font-size: 25px;
    margin-top: 10px;
}

#contactForm div input {
    padding: 10px;
    font-size: 15px;
    border-radius: 10px;
}

#contactForm div button{
    font-size: 25px;
    margin-top: 10px;
    padding: 5px 20px;
    border-radius: 10px;
    cursor: pointer;
}

#contactForm div button:hover{
    background-color: red;
}

.displayContacts p{
    font-size: 20px;
    margin-top: 10px;
}

.displayContacts p button{
    font-size: 15px;
    padding: 5px 20px;
    border-radius: 5px;
}

.displayContacts p button:hover{
    background-color: red;
    cursor: pointer;
}